<template>
  <div
    :class="{
      permissions: true,
      'permissions--new-styles': shouldApplyNewStyles
    }"
  >
    <div class="permissions_top">
      <icon
        :name="shouldApplyNewStyles ? 'logo-name--colorful' : 'logo-name'"
      ></icon>
    </div>
    <!--Top captions with new styles on-->
    <template v-if="shouldApplyNewStyles">
      <div class="permissions_warning">
        {{ $t('permissions.warning') }},
        {{ $t('permissions.password').toLowerCase() }}:
      </div>
    </template>
    <!--Top captions with new styles off-->
    <template v-else>
      <div class="permissions_warning">
        {{ $t('permissions.warning') }}
      </div>
      <div class="permissions_hint">
        {{ $t('permissions.password') }}
      </div>
    </template>
    <div v-show="error" class="permissions_error warning">
      {{ $t('permissions.wrong') }}
    </div>
    <numpad
      class="permissions_numpad"
      data-qa="numpad"
      :subscribe="true"
      :height="shouldApplyNewStyles ? '222px' : '400px'"
      :should-apply-new-styles="shouldApplyNewStyles"
      @change="numberChanged"
      @finish="numberEntered"
    >
    </numpad>
    <div class="permissions_forgot">
      {{ $t('permissions.forgot') }}
    </div>
    <div class="permissions_btns">
      <glovo-btn
        data-qa="permissions-back-btn"
        :text="$t('permissions.cancel')"
        :type="shouldApplyNewStyles ? 'white-green' : 'white'"
        :uppercase="!shouldApplyNewStyles"
        @clack="back()"
      >
      </glovo-btn>
      <glovo-btn
        data-qa="permissions-submit-btn"
        :text="$t('permissions.login')"
        type="green"
        :uppercase="!shouldApplyNewStyles"
        :bold="true"
        @clack="checkAuth()"
      >
      </glovo-btn>
    </div>
  </div>
</template>

<script>
import Icon from 'common/Icon'
import GlovoBtn from 'common/GlovoBtn'
import Numpad from 'common/Numpad'
import { mapActions, mapGetters } from 'vuex'
import {
  SET_ADMIN_MODE,
  GET_CURRENT_STORE_ADMIN_GETTER
} from '@/modules/authentication'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'

export default {
  name: 'Permissions',
  components: {
    Icon,
    GlovoBtn,
    Numpad
  },
  props: {
    popup: {
      type: Boolean
    }
  },
  data() {
    return {
      password: '',
      error: false
    }
  },
  computed: {
    ...mapGetters('authentication', {
      user: GET_CURRENT_STORE_ADMIN_GETTER
    }),
    ...mapGetters({
      toggles: GET_TOGGLES_GETTER
    }),
    shouldApplyNewStyles() {
      return !!this.toggles['FE_PARTNERS_PERMISSIONS_POPUP_NEW_STYLES']
    }
  },
  methods: {
    ...mapActions('authentication', {
      setAdminMode: SET_ADMIN_MODE
    }),
    checkAuth() {
      this.error = false
      if (this.user.supervisorPassword === this.password) {
        this.setAdminMode(true)
        const path = this.$route.query.redirect || '/'
        if (this.popup) {
          this.$emit('accept')
        } else {
          this.$router.push({ path })
        }
      } else {
        this.error = true
      }
    },
    back() {
      if (this.popup) {
        this.$emit('close')
      } else {
        this.$router.go(-1)
      }
    },
    numberChanged(num) {
      this.error = false
      this.password = num
    },
    numberEntered(num) {
      this.numberChanged(num)
      this.checkAuth()
    }
  }
}
</script>
