<template>
  <div
    :class="{
      numpad: true,
      'numpad--new-styles': shouldApplyNewStyles
    }"
  >
    <input v-model="result" class="numpad_input" type="password" />
    <div class="numpad_keyboard" :style="stlyes">
      <div class="numpad_key" @click="pressed('1')">1</div>
      <div class="numpad_key" @click="pressed('2')">2</div>
      <div class="numpad_key" @click="pressed('3')">3</div>
      <div class="numpad_key" @click="pressed('4')">4</div>
      <div class="numpad_key" @click="pressed('5')">5</div>
      <div class="numpad_key" @click="pressed('6')">6</div>
      <div class="numpad_key" @click="pressed('7')">7</div>
      <div class="numpad_key" @click="pressed('8')">8</div>
      <div class="numpad_key" @click="pressed('9')">9</div>
      <div class="numpad_key numpad_key_0" @click="pressed('0')">0</div>
      <div class="numpad_key numpad_key_clear" @click="remove()"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Numpad',
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '400px'
    },
    subscribe: {
      type: Boolean
    },
    number: {
      type: Boolean
    },
    length: {
      type: Number,
      default: 4
    },
    shouldApplyNewStyles: {
      type: Boolean
    }
  },
  data() {
    return {
      result: '',
      maxLength: this.length,
      stlyes: {
        width: this.width,
        height: this.height
      }
    }
  },
  methods: {
    pressed(char) {
      if (this.result.length === this.maxLength) {
        return
      }
      this.result += char

      if (this.subscribe) {
        this.change()
      }

      if (this.result.length === 4) {
        this.finish()
      }
    },
    remove() {
      if (this.result.length === 0) {
        return
      }
      this.result = this.result.slice(0, -1)
      if (this.subscribe) {
        this.change()
      }
    },
    change() {
      const res = this.number ? parseInt(this.result) : this.result
      this.$emit('change', res)
    },
    finish() {
      const res = this.number ? parseInt(this.result) : this.result
      this.$emit('finish', res)
    }
  }
}
</script>
